import React from "react";
import { ImHome3 } from "react-icons/im";
import styled from "styled-components";
export const HeaderContainer = styled.header`
  width: 100%;
  height: 40px;
  position: absolute;
  top: 0;
`;
export default function Header() {
  return (
    <HeaderContainer>
      <div className="HeaderIcon">
        <ImHome3 />
      </div>
    </HeaderContainer>
  );
}
