import React from "react";
export default function Cube() {
    const Deg = [0, 90, 180, 270];
    return (
        <div className="cube-container">
            <div className="cube">
                <div className="top"></div>
                <div>
                    {Deg.map((index, id) => {
                        return <span style={{ transform: `rotateY(${index}deg) translateZ(10px)` }} key={id} ></span>
                    })}
                </div>
            </div>
        </div>
    );
};