import React, { useState, useEffect } from 'react';
import { BsHeart, BsFillHeartFill } from 'react-icons/bs';
import { Ref, countProperties } from './Firebase.js';
import Loading from './Loading';
export default function Heart() {
    const [data, setData] = useState({});
    const [heart, setHeart] = useState('');
    const [count, setCount] = useState(0);
    const [currentHearts, setCurrentHearts] = useState(0);
    const Counter = () => {
        if (heart !== 'true') {
            setCount(count => count + 1)
            window.localStorage.setItem('isHeart', 'true');
            setTimeout(() => {
                Ref.push(count);
            }, 300);
        };
    };
    useEffect(() => {
        Ref.on("value", snapshot => {
            setData(snapshot.val());
        })
        setCurrentHearts(countProperties(data));
        setHeart(window.localStorage.getItem('isHeart'));
        return () => Ref.off();
    }, [count, data]);
    return (
        <div className='Heart-container' >
            <div className='Heart' tabIndex='1' >
                {heart !== 'true' ? <BsHeart onClick={Counter} /> : <BsFillHeartFill style={{color: "rgb(216, 18, 51)"}} />}
            </div>
            <div id='CurrentCount' style={{ cursor: currentHearts === 0 ? "progress" : "" }}>
                {currentHearts === 0 ? <Loading /> : currentHearts}
            </div>
        </div>
    );
};
