import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import "./styles.css";
import styled from "styled-components";
import CardAboutMe from "./Components/About/CardAboutMe";
import Header from "./Components/About/Card/Header/Header";
const Section = styled.section`
  min-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
`;
export default function App() {
  useEffect(() => {
    AOS.init({
      duration: 2000
    });
    AOS.refresh();
  }, []);
  return (
    <React.Fragment>
      <Section>
        <Header />
        <CardAboutMe data-aos="fade-down" />
      </Section>
    </React.Fragment>
  );
}
